// script.js

// Function to toggle visibility of a section
function toggleSectionVisibility(sectionId) {
  var section = document.getElementById(sectionId);
  section.classList.toggle('hidden'); // Toggle 'hidden' class
}
// Example usage: Toggle visibility of 'Projects' section
document.getElementById('toggle-projects').addEventListener('click', function() {
  toggleSectionVisibility('projects-section');
});